body {
  font-size: 14px;
  background: #212121;
  color: #212121;
  /* background: #e7e614; */
}

.wrapper {
  /* max-width: 375px; */
  /* max-width: 600px; */
  /* height: 500px; */
  margin: 0 auto;
  /* background: #e7e614; */
  background: #fff;
}

.logo p {
  font-size: 24px;
  font-weight: 600;
  /* text-align: center; */
  padding: 15px;
  color: #ffc876;
}

.logo p span {
  color: #ccc;
}

.beer-search {
  position: sticky;
  top: 0;
}

.beer-search input {
  font-size: 14px;
  /* font-weight: 600; */
  padding: 15px;
  width: 100%;
  /* background: #ffc876; */
  background-image: linear-gradient(135deg, #ef538e, #e7e614);
  border: none;
}

.beer-search input::placeholder {
  font-size: 14px;
  line-height: 16px;
  color: #000;
}

.beer-search input:focus {
  outline: none;
}

.beer-search input:focus::placeholder {
  color: transparent;
  transition: 0.3s ease-in-out;
}

.beer-list {
  padding-bottom: 15px;
}

.beer-list-item {
  display: flex;
  /* margin-bottom: 1px; */
  /* border-bottom: 1px solid #e7e614; */
  border-bottom: 1px solid rgba(255, 200, 118, 0.3);
  /* background: #fff; */
}

.beer-list-item-description {
  width: 100%;
  padding: 10px 15px;
}

.beer-list-item-description-title {
  font-weight: 600;
  /* color: #ef538e; */
}

.beer-list-item-description-title span {
  /* display: inline-block; */
  margin-left: 5px;
  padding: 0 3px;
  font-weight: 400;
  font-size: 10px;
  vertical-align: top;
  background: #ffc876;
  color: #fff;
  border-radius: 3px;
}

.beer-list-item-description-brewery {
  font-size: 12px;
  font-weight: 400;
  color: #616161;
}

.beer-list-item-description-style {
  font-size: 12px;
  font-weight: 400;
  color: #616161;
}

.beer-list-item-rate {
  padding: 0 15px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-end;
  /* width: 46px; */
  width: 80px;
  /* background: red; */
}

.beer-list-item-rate-r {
  /* font-weight: 600; */
  font-size: 12px;
  color: #ccc;
}

.beer-list-item-rate-r sup {
  margin-right: 2px;
  font-size: 16px;
  font-weight: 600;
  color: #ffc876;
}

.beer-list-item-rate-r sub {
  margin-left: 3px;
}
